html {
	font-size: 62.5%; /*10px*/
}

body {
	// @include font($base-font-size,$base-line-height);
	
	// color: $color-default;
	// background: $body-background;
	width: 100%;
	// font-feature-settings : "palt";

	// -webkit-font-smoothing: antialiased;
	// -moz-osx-font-smoothing: grayscale;
	// min-width: 1000px;
	// max-width: 100%;

	height: 100%;
	min-width: 1080px;

	@include media(sp){
		min-width: 320px;
	}
}

@include theme(trust){
  font-family: $font-jp;
}
@include theme(cool){
  font-family: $font-jp;
  .font-en {
    font-family: $font-lato;
    font-weight: 300;
  }
}
@include theme(natural){
  font-family: $font-jp;
}

input {
	font-family: $font-jp;	
}

a {
	text-decoration: none;
  // color: $color-link;
  // text-decoration: underline;
}

img {
	width: 100%;
	height: auto;
}

.underline {
	cursor: pointer;
	text-decoration: underline;
	&:hover{
		text-decoration: none;
	}
}

