@charset "utf-8";

.fontsize-s{ @include fontsize(10,20); }
.text-s{	@include font(10,24,80,20,40,80); }

.fontsize-m{ @include fontsize(12,24); }
.text-m{	@include font(12,24,80,24,48,80); }

.fontsize-mm{	@include fontsize(14,28); }
.text-mm{ @include font(14,24,80,30,30,80); }

// done
.fontsize-ml{	@include fontsize(16,40); }
.text-ml{ @include font(16,28,80,36,56,80); }

// done
.fontsize-l{	@include fontsize(18,40); }
.text-l{	@include font(18,30,80,40,66,80); }

.fontsize-lm{	@include fontsize(24,48); }
.text-lm{ @include font(24,40,80,48,64,80); }

.fontsize-ll{	@include fontsize(32,48); }
.text-ll{ @include font(32,40,80,48,30,80); }

.letterspacing-s { @include letterspacing(80,80); }
.letterspacing-m { @include letterspacing(100,100); }
.letterspacing-l { @include letterspacing(80,80); }


@include theme(cool){
  .heading {
    font-family: $font-lato;
    font-weight: 300;
    text-align: center;
    @include font(56,56,80,88,88,80);
    &--posfix {
      margin-top: -60px;
      @include media(sp){
        @include vw(margin-top,-95);
      }
    }
    .divide {
      margin-top: 35px;
      margin-bottom: 35px;
      display: block;
      height: 2px;
      width: 24px;
      margin-left: auto;
      margin-right: auto;
      @include media(sp){
        @include vw(width,50);
        @include vw(margin-top,40);
        @include vw(margin-bottom,40);

      }
    }
  }
}