@charset "utf-8";

////////////////////////
// news-headline

.news-headline {
	&__title {
		@extend .fontsize-ll;
		text-align: center;
		margin-bottom: 34px;
		@include media(sp){
			@include vw(margin-bottom,65);
		}
	}
	&__list {
		width: 848px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 40px;
		@include media(sp){
			width: auto;
		}
	}
	&__item {
		margin-top: 8px;
		@include media(sp){
			@include vw(margin-top,40);
		}
		a {
			display: block;
			@extend .motion-opacity;
			@include media(sp){}
		}
		dl {
			display: flex;
			// align-items: center;
			@extend .text-m;
			// background-color: #fff;
			width: 100%;
			@include media(sp){
				display: block;
			}
		}
		dt {
			// padding: 20px;
			// background-color: $theme-color;
			// color: $theme-color-turn;
			text-align: center;
			width: 848px - 744px;
			@include media(sp){
				@include vw(width,204);
			}
			span {
				display: block;
				position: relative;
				top: 50%;
				transform: translateY(-50%);
				@include media(sp){
					top: 0;
					transform: translateY(0);
					
				}
			}
		}
		dd {
			width: 744px;
			padding: 20px 30px;
			@include media(sp){
				width: auto;
				@include vw(padding,40,70);
			}
		}
	}

	&__button {

	}

}


.news-headline-compact {
	display: flex;
	justify-content: space-between;
	position: relative;
	width: 848px;
	margin-left: auto;
	margin-right: auto;
	@include media(sp){
		display: block;
		width: auto;
	}
	&__divide {
		position: absolute;
		width: 1px;
		height: 100%;
		left: 160px;
		top: 0;
		@include media(sp){
			display: none;
		}
	}
	&__title {
		// @extend .fontsize-ll;
		// text-align: left;
		// margin-bottom: 34px;
		// @include media(sp){
		// 	@include vw(margin-bottom,65);
		// }
		@include media(sp){
			@include vw(margin-bottom,60);
		}
	}
	&__list {
		width: 650px;
		// margin-left: auto;
		// margin-right: auto;
		// margin-bottom: 40px;
		@include media(sp){
			width: auto;
		}
	}
	&__item {
		margin-top: 16px;
		&:first-child{
			margin-top: 0px;
		}
		@include media(sp){
			@include vw(margin-top,40);
		}

		a {
			display: block;
			@extend .motion-opacity;
			@include media(sp){

			}
		}
		dl {
			display: flex;
			// align-items: flex-start;
			@extend .text-m;
			// background-color: #fff;
			width: 100%;
			@include media(sp){
				// display: block;
			}
		}
		dt {
			// padding: 20px;
			// background-color: $theme-color;
			// color: $theme-color-turn;
			// text-align: center;
			width: 650px - 540px;
			@include media(sp){
				@include vw(width,180);
			}
			span {

			}
		}
		dd {
			width: 540px;
			@include media(sp){
				width: auto;
				@include vw(width,440);
				// @include vw(padding,40,70);
			}
		}
	}
	&__link {
		position: absolute;
		left: 0;
		bottom: 0;
		@extend .text-m;
		@include media(sp){
			bottom: auto;
			@include vw(top,20);
			left: auto;
			right: 0;
		}
	}
}


@include theme(trust){
	.news-headline-compact {
		&__title {
			@extend .fontsize-ll;
		}
	}
}
@include theme(cool){
	.news-headline-compact {
		&__title {
			@include font(40,40,80,64,80,80);
		}
	}
}