@charset "utf-8";

.btn-pagetop {
	a {
		display: block;
		// background-color: $theme-color-bg;
		text-align: center;
		padding-top: 27px;
		padding-bottom: 27px;
		@include media(sp){
			@include vw(padding-top,48);
			@include vw(padding-bottom,48);
		}
		img {
			width: 18px;
			@include media(sp){
				@include vw(width,34);
			}
		}
	}
}

.footer {
	padding-top: 60px;
	padding-bottom: 90px;
	@include media(sp){
		@include vw(padding,48,0);
	}
	&__inner {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		width: 100%;
		@include media(sp){
			display: block;
		}
	}
	&__logo {
		width: 100%;
		font-weight: bold;
		@extend .text-l;
		margin-bottom: 30px;
		font-weight: bold;
		
	}
	&__left {
		// display: flex;
		width: 260px;
		order: 1;
		@include media(sp){
			@include vw(padding-bottom,80);
			@include vw(margin-bottom,80);
			border-bottom: 1px solid #000000;
		}
		
	}
	&__right {
		width: 750px;
		order: 2;
		@include media(sp){
			width: auto;
		}
	}

	&__nav {
		@extend .text-mm;
		display: flex;
		// justify-content: space-between;
		flex-wrap: wrap;
		margin-bottom: 10px;
		@include media(sp){

		}
		li {
			width: 33%;
			margin-bottom: 14px;
			@include media(sp){
				@include vw(margin-bottom,50);
				width: 50%;
			}
		}
	}

	&__companyinfo {
		border: 1px solid #000;
		padding: 30px;
		margin-bottom: 40px;
		@include media(sp){}
	}
	&__text1 {
		@extend .text-m;
	}
	&__text2 {
		@extend .text-m;
	}
	&__text3 {
		@extend .text-ml;
		margin-top: 0.5em;
		margin-bottom: 0.5em;
	}

	&__copyright {
		@extend .text-s;
		order: 3;
	}
	&__language {
		// background-color: $theme-color-bg;
		@extend .text-s;
		padding: 5px 25px;
		// border-radius: 20px;
		width: 320px;
		position: relative;
		order: 4;
		@include media(sp){
			width: auto;
			border-radius: 0;
			@include vw(margin-bottom,70);
		}
		&:after {
			content: '';
			position: absolute;
			right: 16px;
			top: 50%;
			background-image: url(../images/ico-select.svg);
			background-repeat: no-repeat;
			background-size: 100%;
			width: 7px;
			height: 4px;
			margin-top: -2px;
			@include media(sp){
				@include vw(width,20);
				@include vw(height,12);
				@include vw(margin-top,-6);
				@include vw(right,33);
			}
		}
	}
}