.container {
	position: relative;
	// z-index: 1;
	width: 1064px;
	padding-left: 20px;
	padding-right: 20px;
	margin-left: auto;
	margin-right: auto;

	@include media(sp){
		@include vw(width,632);
		padding-left: 0;
		padding-right: 0;
	}
}

.column {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	@include media(sp){
		display: block;
	}
	&__item {
		// @include media(sp){
		// 	@include vw(margin-bottom,100);
		// 	&:last-child {
		// 		margin-bottom: 0;
		// 	}
		// }
	}
	&--single {
		display: block;
	}
	&--two_full {
		width: 100%;
		// border-bottom: 1px solid #f1f1f1;
		.column__item {
			width: 50%;
			position: relative;
			// padding-bottom: 64px;
			&:after {
				z-index: -1;
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				width: 1px;
				height: 100%;
				background-color: #f1f1f1;
			}

		}
		.feature__box {
			width: 100% * 416/640;
			// width: px;
		}
	}
	&--two {
		width: 945px;
		margin-left: auto;
		margin-right: auto;
		.column__item {
			width: 416px;
		}
	}
	&--three_full {
		width: 100%;
		// border-bottom: 1px solid #f1f1f1;
		.column__item {
			width: calc(100% / 3);
			position: relative;
			// padding-bottom: 64px;
			&:after {
				z-index: -1;
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				width: 1px;
				height: 100%;
				background-color: #f1f1f1;
			}
		}
		.feature__box {
			// width: 296px;
			width: 100% * 296/428;
		}
	}
	&--three {
		.column__item {
			width: 320px;
			@include media(sp){
				width: auto;
			}
		}
	}
	&--four_full {
		width: 100%;
		// border-bottom: 1px solid #f1f1f1;
		.column__item {
			width: 25%;
			position: relative;
			// padding-bottom: 64px;
			&:after {
				z-index: -1;
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				width: 1px;
				height: 100%;
				background-color: #f1f1f1;
			}
		}
		.feature__box {
			width: 100% * 184/320;
			// min-width: 184px;
		}
	}
	&--four {
		.column__item {
			width: 232px;
		}
	}
	&--two ,
	&--three ,
	&--four {

		@include media(sp){
			@include vw(margin-bottom,0);
			// width: 945px;
			margin-left: auto;
			margin-right: auto;
			@include vw(width,632);


			.column__item {
				width: auto;
				@include vw(margin-bottom,100);
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}
	&--single ,
	&--two_full ,
	&--three_full ,
	&--four_full {
		@include media(sp){
			// width: 100%;
			border-bottom: none;
			.column__item {
				width: auto;
				position: relative;
				padding-bottom: 0;
				&:after {
					display: none;
				}
			}
			.feature__box {
				@include vw(width,632);
			}
		}
	}
}

