.feature {
	overflow: hidden;
	&--flex {
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include media(sp){
			display: block;
			margin-left: auto;
			margin-right: auto;
		}
	}
	&--flex2 {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-direction: row-reverse;
		@include media(sp){
			display: block;
		}
	}
	&__pic {
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center center;
		@include media(sp){}
		&--full {
			// height: 320px;
			// margin-bottom: 40px;
			// @include media(sp){
			// 	@include vw(height,528);
			// 	@include vw(margin-bottom,70);
			// }
		}
		&--full2 {
			// height: 240px;
			// margin-bottom: 30px;
			// @include media(sp){
			// 	@include vw(height,528);
			// 	@include vw(margin-bottom,70);
			// }
		}
		&--img {
			// margin-bottom: 40px;
			// @include media(sp){
			// 	@include vw(margin-bottom,50);
			// }
		}		
		&--middle {
			// width: 616px;
			// height: 436px;
			// @include media(sp){
			// 	width: 100%;
			// 	@include vw(height,447);
			// 	@include vw(margin-bottom,50);
			// }
		}
		&--blur {
			filter: blur(20px);
			width: auto !important;
			top: -20px !important;
			left: -20px !important;
			right: -20px !important;
			bottom: -20px !important;
		}
	}

	&__box {
		&--wide {
			width: 660px;
			@include media(sp){
				width: auto;
			}
		}
		&--basic {
			width: 490px;
			@include media(sp){
				width: auto;
			}
		}
		&--narrow {
			width: 350px;
			@include media(sp){
				width: auto;
			}
		}
	}

	&__heading {
		margin-bottom: 1em;
		@include font(24,40,80,32,56,80);
		@include media(sp){}
	}
	&__heading2 {
		@include font(16,28,80,32,56,80);
		margin-bottom: 1em;
		@include media(sp){}
	}
	&__name {
		@extend .text-ml;
		margin-bottom: 0.5em;
		@include media(sp){}
	}
	&__text {
		@extend .text-m;
		@include media(sp){}
	}
	&__button {
		margin-top: 3em;
		@include media(sp){}
	}
}


