.tar { text-align:right; }
.tac { text-align:center; }
.fll{ float:left; }
.flr{ float:right; }

.mla {margin-left: auto;}

.center {
	margin-left: auto;
	margin-right: auto;
}

.posr {
	position: relative;
}

.clearfix:after {
	height: 0;
	visibility: hidden;
	content: ".";
	display: block;
	clear: both;
	font-size:0;
	line-height:0;
	overflow: hidden;
}

.clearfix {
	_height: 1px;
	min-height: 1px;/*・･*//*/
  height: auto;
  overflow: hidden;
  /**/
}

.show_pc {
	display:block;
	@include media(tb){
		display:none;
	}
	@include media(sp){
		display:none;
	}
}

.show_pctb {
	display:block;
	@include media(tb){
		display:block;
	}
	@include media(sp){
		display:none;
	}
}

.show_tbsp {
	display:none;
	@include media(tb){
		display:block;
	}
	@include media(sp){
		display:block;
	}
}

.show_tb {
	display:none;
	@include media(tb){
		display:block;
	}
	@include media(sp){
		display:none;
	}
}

.show_sp {
	display:none;
	@include media(tb){
		display:none;
	}
	@include media(sp){
		display:block;
	}
}

.op a{
	display: block;
	@include trans(opacity,0.5s,0s,$easeOutCubic);
}
.op a:hover{
	opacity: 0.8;
	@include media(sp){
		opacity: 1;
	}
}

.op-txt {
	a {
		$selector: color;
		@include trans($selector, 0.4s, 0s,$easeInOutCubic);
		.op-color {
			$selector: color;
			@include trans($selector, 0.4s, 0s,$easeInOutCubic);
		}
		i{
			$selector: background-image;
			@include trans($selector, 0.4s, 0s,$easeInOutCubic);
		}
		.op-bor{
			$selector: border;
			@include trans($selector, 0.4s, 0s,$easeInOutCubic);
		}
		.op-trans{
			$selector: transform;
			@include trans($selector, 0.4s, 0s,$easeInOutCubic);
		}

	}
}




// スペーサー
@each $key, $value in $spacers {
  .spacer-bottom-#{$key} {

    margin-bottom: #{$value}px;

		@include media(sp){
			@include vw(margin-bottom,$value*2);
		}
  }
  .spacer-top-#{$key} {
    margin-top: #{$value}px;
		@include media(sp){
			@include vw(margin-top,$value*2);
		}
  }
}

