@charset "utf-8";

////////////////////////
// トップメイン

.top-main {
	height: 100vh;
	min-width: 1064px;
	position: relative;
	@include media(sp){
		min-width: 1px;
	}
	&__pic {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-size: cover;
		background-position: center center;
		@include media(sp){}
	}
	&__pic_cover {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		// background-color: #000;
		opacity: 0.75;
	}	
	// -------------
	&__header {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 72px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include media(sp){
			@include vw(height,118);
		}
	}

	&__inner {
		display: flex;
		align-items: center;
		height: 72px;
		position: absolute;
		right: 0;
		top: 0;
		@include media(sp){
			top: auto;
			bottom: 0;
			width: 100%;
			@include vw(height,144);
			
		}
	}
	&__logo {
		@extend .fontsize-l;
		@extend .letterspacing-m;
		padding-left: 40px;
		@include media(sp){

		}
	}
	&__info {
		padding-left: 50px;
		@include media(sp){
			@include vw(padding-left,140);
			padding-top: 0;
		}
		.ico-tel {
			content: '';
			position: absolute;
			left: 0;
			top: 50%;
			width: 40px;
			height: 40px;
			background-image: url(../images/ico-tel.svg);
			background-repeat: no-repeat;
			background-size: 14px;
			background-position: center center;
			margin-top: -20px;
			@include media(sp){
				@include vw(width,102);
				@include vw(background-size,39);
				height: 100%;
				margin-top: 0;
				top: 0;
			}
		}

	}
	&__tel {
		@extend .fontsize-ml;
		@extend .letterspacing-m;
		margin-bottom: 6px;
	}
	&__business_hours {
		@extend .fontsize-s;
		@extend .letterspacing-m;
		white-space: nowrap;
		text-indent: -0.5em;
		@include media(sp){
			text-indent: 0;
			span {
				display: none;
			}
		}
	}
	&__sns {
		display: flex;
		height: 100%;
		margin-left: 40px;
		@include media(sp){
			display: none;
		}
		li {
			text-align: center;
			@include media(sp){}
			a {
				@extend .motion-opacity;
				display: block;
				height: 100%;
				background-repeat: no-repeat;
				background-position: center center;
				@include media(sp){}
			}
		}
	}
	&__facebook {
		width: 72px;
		a {
			background-color: $color-facebook;
			background-image: url(../images/ico-facebook.svg);
			background-size: 23px;
		}
	}
	&__twitter {
		width: 72px;
		a {
			background-color: $color-twitter;
			background-image: url(../images/ico-twitter.svg);
			background-size: 23px;
		}
	}

	// -------------
	&__inner2 {
		position: relative;
		height: 100%;
		@include media(sp){}
	}
	&__box {
		position: absolute;
		color: #fff;
		&--bottom {
			left: 0;
			bottom: 25%;
			@include media(sp){
				@include vw(bottom,220);
			}
		}
		&--center {
			left: 0;
			top: 50%;
			width: 100%;
			text-align: center;
			transform: translateY(-50%);
			@include media(sp){
				// @include vw(top,220);
			}
		}

	}
	&__catch {
		&--jp {
			@include font(40,56,100,58,80,100);
			margin-bottom: 5px;
			@include media(sp){
			}			
		}
		&--en {
			@include font(88,96,40,112,120,40);
			margin-bottom: 5px;
			font-weight: 300;
			@include media(sp){
			}
		}

	}
	&__lead {
		@include font(16,32,100,24,48,100);
	}
	&__lead2 {
		@include font(24,48,100,32,68,100);
	}


	// -------------
	&__nav {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		// background-color: #fff;
		@include media(sp){
			display: none;
		}
	}
	&__list {
		text-align: center;
		@include media(sp){}
	}

	&__item {
		display: inline-block;
		@include media(sp){}
		a {
			display: block;
			padding: 24px;
			@include font(14,14,100,30,30,80);
			@include media(sp){}
		}
	}
}

