@charset "utf-8";
///////////////////////////////////////////
// color.scss
///////////////////////////////////////////

// バックグランド
$body-background: #ffffff;

// 基本テキストカラー
$color-default : #000000 ;

// リンクカラー
$color-link : #000000 ;

// ホバーリンクカラー
$color-link-hover : #000000 ;

// 訪問済みリンクカラー
$color-link-visited : #000000 ;

// セレクションカラー
$selection-color: #cccccc;

// Facebookカラー
$color-facebook : #3b5998 ;

// Twitterカラー
$color-twitter : #1da1f2 ;

// $theme-color: #556fb5;
// $theme-color-turn: #ffffff;
// $theme-color-button: #ffffff;
// $theme-color-bg: #f0f0f0;
// $theme-color-bg-base: #ffffff;

$color-schemes: (
	black:(
		color-basic: (
			color : #000000
			),
		color-border: (
			background-color : #000000
			),
		text-link: (
			color : #000000
			),
		color-main: (
			color: #213d86
			),
		color-bg_nav: (
			background-color : #ffffff
			),
		color-bg_basic: (
			background-color : #ffffff
			),
		color-bg_main: (
			background-color : #213d86,
			color: #fff
			),
		color-button: (
			background-color : #213d86,
			color: #fff
			),
		color-bg_sub: (
			background-color : #eeeeee
			),
		top-main__pic_cover: (
			background-color: #000,
			opacity: 0.15
			)
		),
	blue:(
		color-basic: (
			color : #000000
			),
		color-border: (
			background-color : #000000
			),
		text-link: (
			color : #000000
			),
		color-main: (
			color: #556fb5
			),
		color-bg_nav: (
			background-color : #ffffff
			),
		color-bg_basic: (
			background-color : #ffffff
			),
		color-bg_main: (
			background-color : #556fb5,
			color: #fff
			),
		color-button: (
			background-color : #556fb5,
			color: #fff
			),
		color-bg_sub: (
			background-color : #f0f0f0
			),
		top-main__pic_cover: (
			background-color: #000,
			opacity: 0.15
			)
		),
	green:(
		color-basic: (
			color : #000000
			),
		color-border: (
			background-color : #000000
			),
		text-link: (
			color : #000000
			),
		color-main: (
			color: #3ab0ad
			),
		color-bg_nav: (
			background-color : #ffffff
			),
		color-bg_basic: (
			background-color : #ffffff
			),
		color-bg_main: (
			background-color : #3ab0ad,
			color: #fff
			),
		color-button: (
			background-color : #3ab0ad,
			color: #fff
			),
		color-bg_sub: (
			background-color : #f0f0f0
			),
		top-main__pic_cover: (
			background-color: #000,
			opacity: 0.15
			)
		),
	dark:(
		color-basic: (
			color : #ffffff
			),
		color-border: (
			background-color : #ffffff
			),
		text-link: (
			color : #ffffff
			),
		color-main: (
			color: transparent
			),
		color-bg_nav: (
			background-color : transparent
			),
		color-bg_basic: (
			background-color : #000
			),
		color-bg_main: (
			background-color : transparent,
			color: #fff
			),
		color-button: (
			background-color : transparent,
			color: #fff,
			border: 2px solid #fff
			),
		color-bg_sub: (
			background-color : #14171f
			),
		color-cover: (
			background-image: linear-gradient( 145deg, rgb(37,84,83) 0%, rgb(10,13,74) 100%)
			)
		),

);

@each $color,$values in $color-schemes {
  .color-scheme--#{$color} {
	  @each $key, $propaties in $values {
		  .#{$key} {
		  	@each $propatie, $value in $propaties {
		    	#{$propatie}: #{$value};
		  	}
		  }
	  }
  }
}


