@charset "utf-8";

.button {
	a {
		display: block;
		text-align: center;
		@include font(12,12,260,24,24,260);
		padding: 12px;
		@extend .motion-opacity;
		@include media(sp){
			@include vw(padding,28);
		}
	}
	&--short {
		width: 184px;
		@include media(sp){
			width: auto;
		}

	}
	&--large {
		width: 232px;
		@include media(sp){
			width: auto;
		}
	}
	&--center {
		margin-left: auto;
		margin-right: auto;
	}
}

