@charset "utf-8";

////////////////////////
// company-summary

.company-summary {
	display: flex;
	justify-content: space-between;
	align-items: center;
	@include media(sp){
		display: block;
	}
	&__map {
		width: 616px;
		height: 436px;
		@include media(sp){

			@include vw(margin-left,-59);
			@include vw(margin-bottom,80);
			@include vw(width,750);
			@include vw(height,532);

		}
	}
	&__box {
		width: 360px;
		@include media(sp){
			width: auto;
		}
	}
	&__heading {
		@extend .text-lm;
		margin-bottom: 1em;
		@include media(sp){}
		// @extend .letterspacing-m;
	}
	&__name {
		@extend .text-ml;
		margin-bottom: 0.5em;
		@include media(sp){}
		// @extend .letterspacing-m;
	}
	&__text {
		@extend .text-m;
		margin-bottom: 3em;
		@include media(sp){}
	}
}